import MockGameStateService from "./MockGameStateService";

/*
* Use the random game seed & encounter ID to determine initiative values
* in a random but recreateable way
*/
export default class DiceRoller {
    public constructor(id: string) {
        const hashCode = (s: string) =>
            s.split("").reduce((a, b) => {
                a = (a << 5) - a + b.charCodeAt(0);
                return a & a;
            }, 0);

        const service = new MockGameStateService();
        const state = service.getState();
        this._encounterSeed = hashCode(id) ^ state.seed;
    }

    private _encounterSeed: number;

    private generate(): number {
        const result = DiceRoller.mulberry32(this._encounterSeed);
        this._encounterSeed = result * Number.MAX_SAFE_INTEGER;
        return result;
    }

    public rollDie = (dieType: number): number => {
        return Math.ceil(this.generate() * dieType);
    };

    public roll20 = (): number => this.rollDie(20);

    public roll10 = (): number => this.rollDie(10);

    private static mulberry32(a: number): number {
        var t = (a += 0x6d2b79f5);
        t = Math.imul(t ^ (t >>> 15), t | 1);
        t ^= t + Math.imul(t ^ (t >>> 7), t | 61);
        return ((t ^ (t >>> 14)) >>> 0) / 4294967296;
    }
}
